.registration {
  &__title {
    br {
      display: none;
      @include screen(tablet-2x) {
        display: block;
      }
    }
    @include screen(tablet-2x) {
      text-align: left;
      position: sticky;
      top: 106px;
      margin-bottom: 220px;
    }
  }
  &__wrapper {
    display: grid;
    gap: 40px;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    @include screen(tablet-2x) {
      gap: 0;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  &__items {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: flex-start;
    @include screen(tablet-2x) {
      gap: 24px;
    }
  }
  &__item {
    visibility: hidden;
    background-color: $white;
    padding: 40px;
    border-radius: 32px;
    counter-increment: list;
    @include screen(tablet-2x) {
      border-radius: 40px;
      padding: 50px 40px 40px;
    }
  }
  &__icon {
    display: inline-flex;
    align-items: center;
    position: relative;
    &:after {
      font-family: $bebas;
      content: "0" counter(list);
      position: relative;
      font-weight: 700;
      font-size: 22px;
      line-height: 22px;
      right: -3px;
      letter-spacing: 0.01em;
      color: $black-100;
      @include screen(tablet-2x) {
        font-size: 28px;
        line-height: 28px;
      }
    }
    img {
      margin-top: -6px;
    }
  }
  &__subtitle {
    margin-top: 32px;
    font-weight: 500;
    font-size: 18px;
    line-height: 23px;
    @include screen(tablet-2x) {
      line-height: 28px;
      font-size: 22px;
    }
  }
  &__subtext {
    font-size: 16px;
    line-height: 22px;
    margin-top: 8px;
    @include screen(tablet-2x) {
      font-size: 18px;
      line-height: 25px;
    }
    a {
      color: $orange;
      border-bottom: 1px solid transparent;
      @include screen(laptop) {
        transition: border-color $transition-default;
        &:hover {
          border-color: $orange;
        }
      }
    }
  }
  &__link {
    margin-top: 4px;
    background: $orange-gradient;
    padding: 12px;
    width: 100%;
    border-radius: 12px;
    text-align: center;
    visibility: hidden;
    color: $white;
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
    background-color: $orange;
    @include screen(tablet-2x) {
      font-size: 22px;
      line-height: 28px;
      padding: 14px;
      margin-top: 26px;
    }
    @include screen(laptop) {
      &:hover {
        background: $button-main-hover;
      }
      &:active {
        background: $button-main-pressed;
      }
    }
  }
}