.header {
  position: fixed;
  visibility: hidden;
  width: 100%;
  top: 8px;
  z-index: 100;
  @include screen(tablet) {
    top: 12px;
  }
  @include screen(tablet-2x) {
    top: 20px;
  }
  &__wrapper {
    background: linear-gradient(110.13deg, #262626 23.3%, #1C1C1C 83.03%);
    border-radius: 16px;
    position: relative;
    padding: 0 7px 0 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: calc(100% - 16px);
    margin: 0 8px;
    @include screen(tablet) {
      max-width: 576px;
      margin: 0 auto;
    }
    @include screen(tablet-2x) {
      padding: 12px 24px;
      max-width: 100%;
      border-radius: 24px;
    }
    @include screen(laptop) {
      max-width: 1360px;
    }
  }
  &__logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    @include screen(tablet-2x) {
      position: static;
      transform: translateX(0);
    }
    img {
      max-width: 68px;
      @include screen(tablet-2x) {
        max-width: 100%;
      }
    }
  }
  &__nav {
    display: none;
    @include screen(tablet-2x) {
      display: flex;
      gap: 20px;
      align-items: center;
    }
  }
  &__navmob {
    width: 100%;
    top: 51px;
    position: absolute;
    padding: 16px 0;
    display: none;
    z-index: 50;
    border-radius: 16px;
    background: linear-gradient(110.13deg, #262626 23.3%, #1C1C1C 83.03%);
    @include screen(tablet) {
      top: 59px;
      max-width: 155px;
      min-width: 155px;
    }
    &.active {
      display: block;
    }
    .header__link {
      display: block;
      padding: 20px 24px 12px;
      font-family: $bebas;
      text-transform: uppercase;
      font-size: 24px;
      line-height: 29px;
      transition: background-color $transition-default, color $transition-default;
      &:hover {
        background-color: $gray-400;
        color: $orange;
      }
      @include screen(tablet) {
        font-size: 16px;
        line-height: 20px;
        padding: 10px 24px 6px;
      }
    }
  }
  &__link {
    font-size: 16px;
    font-weight: 500;
    line-height: 21px;
    color: $white;
    @include screen(laptop) {
      transition: color $transition-default;
      &:hover {
        color: $orange;
      }
    }
  }
  &__button {
    background: $orange-gradient;
    background-color: $orange;
    color: $white;
    padding: 7.5px 16px 7.5px 16px;
    font-weight: 700;
    border-radius: 8px;
    font-size: 14px;
    line-height: 18px;
    transition: $transition-default;
    @include screen(tablet-2x) {
      border-radius: 12px;
      font-size: 16px;
      padding: 14px 24px 14px 24px;;
      font-weight: 700;
      line-height: 21px;
    }
    @include screen(laptop) {
      &:hover {
        background: $button-main-hover;
      }
      &:active {
        background: $button-main-pressed;
      }
    }
    &:before {
      content: attr(data-label);
      position: relative;
      font-size: 14px;
      line-height: 18px;
      color: $white;
      @include screen(tablet) {
        display: none;
      }
    }
    span {
      display: none;
      position: relative;
      @include screen(tablet) {
        display: block;
      }
    }
  }
  &__burger {
    min-width: 57px;
    min-height: 47px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 11px 16px;
    border-radius: 16px 0 0 16px;
    transition: background-color $transition-default;
    &:hover {
      background-color: $gray-400;
    }
    @include screen(tablet-2x) {
      display: none;
    }
    svg {
      rect {
        transition: transform $transition-default, opacity $transition-default, fill $transition-default;
      }
    }
    &.active {
      background-color: $gray-400;
      svg {
        rect {
          fill: $orange;
          &:nth-child(1) {
            transform: translateX(3px) rotate(45deg);
          }
          &:nth-child(2) {
            opacity: 0;
          }
          &:nth-child(3) {
            width: 16px;
            transform: translateX(-7px) translateY(3px) rotate(-45deg);
          }
        }
      }
    }
  }
}