.contacts {
  &__bg {
    position: absolute;
    top: -313px;
    left: -400px;
    max-width: 650px;
    @include screen(tablet) {
      left: -212px;
    }
    @include screen(tablet-2x) {
      top: auto;
      bottom: -229px;
      left: 37px;
    }
    @include screen(laptop) {
      max-width: 100%;
      bottom: -285px;
    }
  }
  &__title {
    @include screen(tablet-2x) {
      text-align: left;
    }
  }
  &__text {
    max-width: 508px;
    margin: 0 auto;
    @include screen(tablet-2x) {
      max-width: 100%;
    }
    p {
      font-size: 16px;
      line-height: 22px;
      visibility: hidden;
      margin-top: 8px;
      color: $white;
      text-align: center;
      @include screen(tablet) {
        font-size: 18px;
        line-height: 25px;
      }
      @include screen(tablet-2x) {
        text-align: left;
        margin-top: 16px;
        font-size: 22px;
        line-height: 31px;
      }
    }
  }
  &__wrapper {
    position: relative;
    overflow: hidden;
    display: grid;
    gap: 40px;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    @include screen(tablet-2x) {
      gap: 0;
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__block {
    @include screen(tablet-2x) {
      grid-column: span 6 / span 6;
    }
  }
  &__form {
    visibility: hidden;
    max-width: 508px;
    margin: 0 auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 12px;
    align-items: flex-start;
    @include screen(tablet-2x) {
      grid-column: span 5 / span 5;
      grid-column-start: 8;
    }
  }
  &__label {
    cursor: pointer;
    width: 100%;
  }
  &__input,
  &__textarea {
    display: flex;
    position: relative;
    input,
    textarea {
      width: 100%;
      border-radius: 12px;
      background-color: $input-default;
      color: $white;
      padding: 11px 64px 11px 24px;
      border: 3px solid transparent;
      transition: border-color $transition-default, background-color $transition-default;
      @include screen(tablet-2x) {
        padding: 16px 64px 16px 24px;
      }
      &:active,
      &:focus {
        border-color: $white;
        background-color: $input-active
      }
      &:hover {
        background-color: $input-active
      }
    }
    &.error {
      input {
        border-color: red;
      }
    }
  }
  &__textarea {
    textarea {
      resize: none;
      min-height: 155px;
      display: block;
    }
  }
  &__button {
    background-color: $white;
    border-radius: 12px;
    color: $orange;
    padding: 12px;
    width: 100%;
    font-size: 16px;
    line-height: 20px;
    font-weight: 700;
    text-align: center;
    transition: filter $transition-default, opacity $transition-default;
    &.disabled {
      opacity: 0.8;
      filter: grayscale(100%);
      cursor: not-allowed;
    }
    @include screen(tablet-2x) {
      line-height: 29px;
      padding: 14px;
      font-size: 22px;
    }
    @include screen(laptop) {
      transition: background-color $transition-default, color $transition-default;
      &:not(.disabled):hover {
        background-color: $button-white-hover;
        color: $white;
      }
      &:not(.disabled):active {
        background-color: $button-white-pressed;
        color: $white;
      }
    }
  }
  &__icon {
    position: absolute;
    right: 24px;
    z-index: 2;
    top: 19px;
    opacity: 0;
    transition: opacity $transition-default;
    &.active {
      opacity: 1;
    }
  }
}