* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}

textarea,
input,
button,
select,
a {
  outline: none;
  border-radius: 0;

  font-weight: 400;
  font-family: 'PT Root UI';
  touch-action: manipulation;
}

textarea,
input {
  &::placeholder {
    color: $text-input-placeholder;
  }
}

html,
body {
  font-family: 'PT Root UI';
  background-color: $black-100;
}

.render {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;

  width: 100%;
  height: 100%;

  background-color: $black-100;
}

main {
  margin-top: 63px;
  @include screen(tablet) {
    margin-top: 71px;
  }
  @include screen(tablet-2x) {
    margin-top: 113px;
  }
}

.wrapper {
  margin: 0 auto;
  padding: 0 8px;
  width: 100%;
  @include screen(tablet) {
    padding: 0 16px;
  }
  @include screen(tablet-2x) {
    padding: 0 24px;
  }
}

.h1,
.h2 {
  font-family: $bebas;
  font-size: 34px;
  font-weight: 700;
  visibility: hidden;
  line-height: 40px;
  letter-spacing: 0.01em;
  text-align: center;
  @include screen(tablet) {
    font-size: 60px;
    line-height: 72px;
  }
}

.section {
  border-radius: 24px;
  padding: 50px 24px;
  margin-bottom: 8px;
  @include screen(tablet) {
    padding: 60px 106px 90px;
    margin-bottom: 12px;
    border-radius: 40px;
  }
  @include screen(tablet-2x) {
    padding: 90px 106px 110px;
    border-radius: 60px;
    margin-bottom: 20px;
  }
  @include screen(laptop) {
    padding: 100px 106px 110px;
  }
  @include screen(desktop) {
    padding: 110px 106px 150px;
  }
  &-gray {
    background-color: $gray;
    .h1,
    .h2 {
      color: $black-100;
    }
  }
  &-orange {
    background: $orange-gradient;
    .h1,
    .h2 {
      color: $white;
    }
  }
  &-black-90 {
    background-color: $black-90;
    border: 1px solid $black-70;
    .h1,
    .h2 {
      color: $white;
    }
  }
  &-no-pb {
    padding-bottom: 0;
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}