.conditions {
  &__text {
    text-align: center;
    margin-top: 8px;
    font-size: 16px;
    visibility: hidden;
    line-height: 22px;
    color: $text-input-placeholder;
    @include screen(tablet) {
      font-size: 18px;
      line-height: 25px;
    }
    @include screen(tablet-2x) {
      font-size: 22px;
      line-height: 31px;
      margin-top: 12px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 20px;
    margin-top: 40px;
    @include screen(tablet-2x) {
      margin-top: 50px;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(laptop) {
      margin-top: 60px;
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
  }
  &__item {
    border-radius: 32px;
    background-color: $black-70;
    padding: 28px 32px 32px;
    display: flex;
    visibility: hidden;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    @include screen(tablet-2x) {
      min-height: 200px;
    }
    @include screen(laptop) {
      min-height: 308px;
    }
  }
  &__icon {
    margin-bottom: 32px;
    @include screen(tablet-2x) {
      margin-bottom: 0;
    }
    img {
      width: 56px;
      @include screen(tablet-2x) {
        width: 76px;
      }
    }
  }
  &__subtitle {
    font-size: 16px;
    line-height: 22px;
    color: $white;
  }
}