.footer {
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 32px;
    @include screen(tablet) {
      gap: 50px;
    }
    @include screen(tablet-2x) {
      display: grid;
      grid-template-columns: repeat(12, minmax(0, 1fr));
      gap: 0;
    }
  }
  &__block {
    visibility: hidden;
    @include screen(tablet-2x) {
      grid-column: span 5 / span 5;
    }
  }
  &__info {
    margin-top: 32px;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    gap: 8px;
  }
  &__contacts {
    visibility: hidden;
    display: flex;
    gap: 12px;
    flex-direction: column;
    align-items: flex-start;
    @include screen(tablet) {
      gap: 16px;
    }
    @include screen(tablet-2x) {
      gap: 20px;
      grid-column: span 6 / span 6;
      grid-column-start: 7;
    }
    @include screen(laptop) {
      gap: 20px;
      grid-column: span 5 / span 5;
      grid-column-start: 8;
    }
  }
  &__text {
    color: $text-input-placeholder;
    font-size: 16px;
    line-height: 22px;
    @include screen(tablet) {
      font-size: 18px;
      line-height: 25px;
    }
    @include screen(tablet-2x) {
      line-height: 30px;
      font-size: 22px;
    }
  }
  &__links {
    display: flex;
    flex-direction: column;
    gap: 4px;
    align-items: flex-start;
  }
  &__link {
    font-family: $bebas;
    font-weight: 700;
    letter-spacing: 0.01em;
    color: $white;
    font-size: 24px;
    line-height: 28px;
    @include screen(tablet) {
      font-size: 28px;
      line-height: 34px;
    }
    @include screen(tablet-2x) {
      line-height: 52px;
      font-size: 44px;
    }
    @include screen(laptop) {
      transition: color $transition-default;
      &:hover {
        color: $orange;
      }
    }
  }
  &__data {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 12px;
    line-height: 16px;
    @include screen(tablet) {
      font-size: 16px;
      line-height: 22px;
    }
    color: $text-input-placeholder;
    @include screen(tablet-2x) {
      font-size: 18px;
      line-height: 25px;
    }
    p {
      width: 100%;
      max-width: 64px;
    }
  }
  &__address {
    address {
      color: $text-input-placeholder;
      display: inline-block;
      font-style: normal;
      font-size: 12px;
      line-height: 16px;
      @include screen(tablet) {
        font-size: 16px;
        line-height: 22px;
      }
      @include screen(tablet-2x) {
        line-height: 25px;
        font-size: 18px;
      }
      @include screen(laptop) {
        transition: color $transition-default;
      }
      br {
        display: none;
        @include screen(tablet) {
          display: block;
        }
        @include screen(tablet-2x) {
          display: none;
        }
      }
    }
    @include screen(laptop) {
      &:hover {
        address {
          color: $orange;
        }
      }
    }
  }
  &__copy {
    visibility: hidden;
    grid-column: span 12 / span 12;
    display: flex;
    flex-direction: column-reverse;
    font-size: 16px;
    line-height: 22px;
    gap: 32px;
    @include screen(tablet) {
      align-items: center;
      flex-direction: row;
      gap: 50px;
    }
    @include screen(tablet-2x) {
      margin-top: 50px;
    }
    p {
      color: $text-input-placeholder;
      @include screen(tablet-2x) {
        font-size: 18px;
        line-height: 25px;
      }
    }
  }
  &__policy {
    font-size: 16px;
    line-height: 22px;
    color: $white;
    border-bottom: 1px solid transparent;
    @include screen(tablet-2x) {
      font-size: 18px;
      line-height: 25px;
    }
    @include screen(laptop) {
      transition: border-color $transition-default;
      &:hover {
        border-color: $white;
      }
    }
  }
}