.info {
  &__text {
    text-align: center;
    margin-top: 8px;
    font-size: 16px;
    visibility: hidden;
    line-height: 22px;
    @include screen(tablet) {
      font-size: 18px;
      line-height: 25px;
    }
    @include screen(tablet-2x) {
      font-size: 22px;
      line-height: 31px;
      margin-top: 12px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 20px;
    counter-reset: list;
    margin-top: 40px;
    @include screen(tablet-2x) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      margin-top: 50px;
    }
    @include screen(laptop) {
      margin-top: 60px;
    }
  }
  &__item {
    visibility: hidden;
    background-color: $white;
    border-radius: 40px;
    padding: 50px 40px 40px;
    counter-increment: list;
  }
  &__icon {
    display: inline-flex;
    align-items: center;
    position: relative;
    &:after {
      font-family: $bebas;
      content: "0" counter(list);
      font-size: 28px;
      position: relative;
      font-weight: 700;
      right: -3px;
      line-height: 28px;
      letter-spacing: 0.01em;
      color: $black-100;
    }
    img {
      margin-top: -6px;
    }
  }
  &__subtitle {
    font-weight: 500;
    font-size: 18px;
    margin-top: 24px;
    line-height: 23px;
    text-align: left;
    @include screen(tablet-2x) {
      line-height: 28px;
      margin-top: 32px;
      font-size: 22px;
    }
  }
  &__subtext {
    margin-top: 16px;
    font-size: 16px;
    line-height: 22px;
    @include screen(tablet-2x) {
      font-size: 18px;
      line-height: 25px;
    }
  }
  &__list {
    margin-top: 12px;
    margin-left: 16px;
    @include screen(tablet-2x) {
      margin-top: 16px;
    }
  }
  &__li {
    margin-top: 8px;
    position: relative;
    font-size: 16px;
    line-height: 22px;
    @include screen(tablet-2x) {
      font-size: 18px;
      line-height: 25px;
    }
    &:before {
      content: '';
      position: absolute;
      width: 6px;
      height: 6px;
      background-color: $orange;
      border-radius: 50%;
      left: -16px;
      top: 9px;
    }
  }
}