.hero {
  &__wrapper {
    overflow: hidden;
    position: relative;
  }
  &__bg {
    position: absolute;
    &-left {
      left: -400px;
      transform: scale(0.7);
      bottom: 0;
      max-width: 840px;
      @include screen(tablet) {
        transform: scale(1);
        bottom: -200px;
        left: 0;
      }
      @include screen(tablet-2x) {
        bottom: -250px;
        max-width: 100%;
      }
      @include screen(laptop) {
        bottom: -200px;
        left: -200px;
      }
      @include screen(desktop) {
        bottom: 0;
      }
    }
    &-right {
      max-width: 400px;
      right: -200px;
      @include screen(tablet) {
        right: 0;
      }
      @include screen(tablet-2x) {
        max-width: 100%;
        bottom: -200px;
        right: -650px;
      }
      @include screen(laptop) {
        right: -500px;
      }
      @include screen(desktop) {
        bottom: 0;
        right: 0;
      }
    }
  }
  &__image {
    max-width: 539px;
    min-width: 539px;
    visibility: hidden;
    margin: 20px auto 0;
    @include screen(tablet) {
      max-width: 1586px;
      margin-top: 30px;
    }
    @include screen(tablet-2x) {
      max-width: 1052px;
      margin-top: 40px;
    }
    @include screen(laptop) {
      margin-top: 60px;
      max-width: 1165px;
    }
    @include screen(desktop) {
      max-width: 1416px;
    }
    img {
      width: 100%;
    }
  }
}