.about {
  &__bg {
    width: 100%;
    @include screen(tablet) {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
    }
    h2 {
      font-weight: 700;
      font-family: $bebas;
      text-align: center;
      background-color: #f3ec78;
      background-image: linear-gradient(155.53deg, #FF5C00 8.96%, #FFA800 122.56%);
      background-size: 100%;
      -webkit-background-clip: text;
      -moz-background-clip: text;
      -webkit-text-fill-color: transparent; 
      -moz-text-fill-color: transparent;
      font-size: 110px;
      font-weight: 700;
      line-height: 110px;
      @include screen(tablet) {
        font-size: 200px;
        line-height: 200px;
        position: sticky;
        top: 116px;
      }
      @include screen(tablet-2x) {
        font-size: 330px;
        line-height: 330px;
      }
    }
  }
  &__container {
    position: relative;
    @include screen(tablet) {
      padding: 100px 0 0;
    }
    @include screen(tablet-2x) {
      padding: 230px 0 0;
    }
    @include screen(laptop) {
      padding: 240px 0 0;
    }
  }
  &__items {
    display: flex;
    flex-direction: column;
    gap: 24px;
    align-items: center;
    z-index: 3;
    position: relative;
    padding: 0 8px;
    @include screen(tablet) {
      padding: 0;
    }
  }
  &__item {
    background: linear-gradient(110.13deg, #262626 23.3%, #1C1C1C 83.03%);
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    gap: 12px;
    padding: 60px 50px 50px 50px;
    border-radius: 32px;
    @include screen(tablet) {
      max-width: 353px;
    }
    @include screen(tablet-2x) {
      border-radius: 40px;
      max-width: 420px;
    }
  }
  &__subtitle {
    font-size: 70px;
    line-height: 70px;
    letter-spacing: 0.01em;
    font-weight: 700;
    text-align: center;
    font-family: $bebas;
    color: $white;
    @include screen(tablet-2x) {
      font-size: 100px;
      line-height: 100px;
    }
  }
  &__subtext {
    font-size: 18px;
    line-height: 25px;
    font-weight: 400;
    text-align: center;
    color: $white;
    @include screen(tablet-2x) {
      line-height: 31px;
      font-size: 22px;
    }
  }
  &-banner {
    z-index: 3;
    position: relative;
    background-color: $black-90;
    margin: 80px auto 0;
    visibility: hidden;
    padding: 60px 40px;
    display: flex;
    border: 5px solid $orange;
    border-radius: 40px;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    gap: 48px;
    @include screen(tablet) {
      max-width: 508px;
    }
    @include screen(tablet-2x) {
      margin: 90px auto 0;
      padding: 50px 70px;
      flex-direction: row;
      max-width: 1020px;
    }
    @include screen(laptop) {
      margin: 100px auto 0;
      max-width: 1180px;
    }
    @include screen(desktop) {
      margin: 120px auto 0;
      max-width: 100%;
    }
    &__title {
      display: flex;
      align-items: center;
      justify-content: center;
      @include screen(tablet-2x) {
        display: block;
      }
    }
    &__text {
      font-size: 22px;
      font-weight: 400;
      line-height: 31px;
      color: $white;
      margin-top: 32px;
      text-align: center;
      @include screen(tablet-2x) {
        text-align: left;
      }
    }
    &__illustration {
      min-width: 200px;
      min-height: 200px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
    }
    &__image {
      animation: rotate 10s linear infinite;
    }
    &__icon {
      position: absolute;
      right: 25px;
    }
  }
}